import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import './App.css';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Header from './Header';
import Home from './Home.jsx';
import Academic from './Academic.jsx';
import InternShip from './Internship'
import Project from './Project'
import Acheivement from './Acheivement'
import Footer from './Footer'
const sections = [
  { title: 'Home', url: '/home' },
  { title: 'Academic', url: '/Academic' },
  { title: 'InternShip', url: '/InternShip' },
  { title: 'Project', url: '/Project' },
  { title: 'Achievement', url: '/Achievement' },  
];

function App() {
  const path = window.location.pathname;  
  return (
    <React.Fragment>
      <Router>
        <CssBaseline />
        <Container maxWidth="lg">
          <Header UserName={"Preethi Hariharan"} title="PortFolio" sections={sections} />          
          <Switch>
            {path === '/' || path === '/Home'?
              <Route exact path="/">
              <Home />
            </Route> :
            <Route exact path="/Home">
              <Home />
            </Route>
          }              
          <Route exact path="/Academic">
            <Academic />
          </Route> 
          <Route exact path="/InternShip">
            <InternShip />
          </Route> 
          <Route exact path="/Project">
            <Project />
          </Route> 
          <Route exact path="/Achievement">
            <Acheivement />
          </Route>                       
        </Switch>
        <Footer title="PortFolio" description="Personalized portfolio Created by Preethi Hariharan" />  
        </Container>         
      </Router>  
    </React.Fragment>
  );
}

export default App;
