import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Avatar from '@material-ui/core/Avatar';
import { deepOrange } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
  toolbar: {
    borderBottom: `1px solid ${theme.palette.divider}`,
  },
  toolbarTitle: {
    flex: 1,
  },
  toolbarSecondary: {
    justifyContent: 'space-between',
    overflowX: 'auto',
  },
  toolbarLink: {
    padding: theme.spacing(1),
    flexShrink: 0,
  },
  orange: {
    color: theme.palette.getContrastText(deepOrange[500]),
    backgroundColor: deepOrange[500],
  },
}));

export default function Header(props) {
  const classes = useStyles(); 
  const { sections, title, UserName } = props;  
  return (
    <React.Fragment>
      <Toolbar className={classes.toolbar}>     
      <Typography
          component="h2"
          variant="h5"
          color="inherit"
          align="left"
          noWrap          
          className={classes.toolbarTitle}
        >
          {UserName}
        </Typography>    
        <Typography
          component="h2"
          variant="h5"
          color="inherit"
          align="left"
          noWrap
          className={classes.toolbarTitle}
        >
          {title}
        </Typography>
        <a href="https://twitter.com/PreethiHarihar3?s=08" style={{marginTop:"5px"}}>
        <img src="data:image/png;base64," alt="" width="40px" height="25px"/>       
        </a>
        <a href="https://www.linkedin.com/in/preethi-hariharan-44a877139" style={{marginTop:"5px",marginRight:"5px"}}>
        <img src="data:image/png;base64," alt="" width="30px" height="30px"/>       
        </a>
        <a href="https://accounts.google.com/" style={{marginTop:"5px",marginRight:"5px"}}>
        <img src="data:image/png;base64," alt="" width="30px" height="20px"/>       
        </a>       
        <Avatar className={classes.orange}>PH</Avatar>
      </Toolbar>  
      <Toolbar component="nav" variant="dense" className={classes.toolbarSecondary}>
        {sections.map((section) => (
          <Link
            color="inherit"
            noWrap
            key={section.title}
            variant="body2"
            href={section.url}
            className={classes.toolbarLink} 
          >
            {section.title}
          </Link>
        ))}
      </Toolbar>        
    </React.Fragment>
  );
}

Header.propTypes = {
  sections: PropTypes.array,
  title: PropTypes.string,
  UserName: PropTypes.string,
};